import { Modal } from "antd";

interface Props {
  visible: boolean;
  setVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export default function ToGithub({ visible, setVisible }: Props) {
  const handleOk = () => {
    setVisible(false);
  };

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <Modal
      title="打扰🌶"
      visible={visible}
      onOk={handleOk}
      onCancel={handleCancel}
      okText={"确定"}
      cancelText={"取消"}
    >
      <p>如果该项目对您有帮助，请点个star让作者知道吧(:</p>
      <a
        className="pointer text-blue-300 mt-4"
        target={"__blank"}
        href="https://github.com/MasonEast/MasonEast.github.io"
      >
        去往Github
      </a>
    </Modal>
  );
}
